@use "base/variables" as var;
@use "base/mixins" as mix;

html {
  box-sizing: border-box;
  font-size: 62.5%;
}
*, *:before, *:after {
  box-sizing: inherit;
}

body{
  font-family: var.$fuente_principal;
  font-size: 1.8rem;
  margin: 1rem auto;
  max-width: 90%;

  @include mix.telefono {
    margin: 20rem auto;
  }
  
  @include mix.desktop {
    max-width: 40%;
  }

  @include mix.telefono {
    // Forma de Pagina y Sombra
    -webkit-box-shadow: 10px 10px 29px -4px rgba(28,26,138,0.35);
    -moz-box-shadow: 10px 10px 29px -4px rgba(28,26,138,0.35);
    box-shadow: 10px 10px 29px -4px rgba(28,26,138,0.35);

    // Esquinas redondeada de forma de pagina
    border-radius: 40px 40px 40px 40px;
    -moz-border-radius: 40px 40px 40px 40px;
    -webkit-border-radius: 40px 40px 40px 40px;
  } 
}

p{
  color: var.$lavanda;
}
