@use "base/mixins" as mix;
@use "base/variables" as var;

body{
    position: relative;
}

.footer{
    position: absolute;

    @include mix.telefono {
        display: flex;
    }

    div{
        display: flex;
        align-items: center;
    }

    &__texto{
        color: var.$negro;
        font-size: 1.4rem;
        margin: 2.5rem .5rem 0 0;

        &:last-of-type{
            @include mix.telefono {
                margin-left: .5rem;
            }   
        }        
    }

    &__enlace{
        text-decoration: none;
        color: var.$azul;
        font-size: 1.4rem;
        margin-top: 2.5rem;
    }
}