@use "base/variables" as var;

@mixin telefono {
    @media (min-width: var.$telefono) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: var.$desktop) {
        @content;
    }
}

@mixin grid($columna, $espacio){
    display: grid;
    grid-template-columns: repeat($columna, 1fr);
    gap: $espacio;
}

@mixin boton($color, $bg){
    display: block;
    color: $color;    
    text-decoration: none;
    text-align: center;
    background-color: $bg;
    border-radius: 4rem;
    padding: 2rem 0;
    margin: 2rem;

    @include telefono{
        margin: 4rem 4rem 5rem 2rem;
    }

    &:hover{
        background: linear-gradient(to bottom, var.$azul-pizarra 20%, var.$azul-real 100%);
    }
}


