@use "base/mixins" as mix;
@use "base/variables" as var;

.sumario{

    &__titulo{
        color: var.$negro;
        margin-left: 2rem;
    }

    &__enlace{
        @include mix.boton(var.$blanco, var.$azul-grisaceo);
    }   
}

.apartado{
    display: flex;
    justify-content: space-between;
    border-radius: 1rem;
    padding: 0 2rem;
    margin: 0 2rem 1.5rem 2rem;

    @include mix.telefono {
        margin: 0 4rem 1.5rem 2rem;
    }

    &--reaccion{
        background-color: lighten(var.$red, 30%);
    }

    &--memoria{
        background-color: lighten(var.$amarillo, 40%);
    }

    &--verbal{
        background-color: lighten(var.$verde, 55%);
    }

    &--visual{
        background-color: lighten(var.$azul, 50%);
    }

    &__valor{
        color: lighten(var.$azul-grisaceo, 35%);
        font-weight: var.$black;

        span{
            color: var.$negro;
        }
    }
}

.sector{
    display: flex;
    align-items: center;

    &__texto{
        font-weight: var.$bold;
        margin-left: 1rem;
    }

    &__texto--reaccion{
        color: var.$red;
    }

    &__texto--memoria{
        color: var.$amarillo;
    }

    &__texto--verbal{
        color: var.$verde;
    }

    &__texto--visual{
        color: var.$azul;
    }
}