@use "base/mixins" as mix;
@use "base/variables" as var;

.contenedor{
    @include mix.telefono {
        @include mix.grid(2, 2rem);
    }   
}

.resultado{
    padding: 1rem 6rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: linear-gradient(to bottom, darken(var.$azul-pizarra, 5%) 20%, var.$azul-real 100%);
    border-radius: 40px;
    position: relative;

    // Creamos el circulo y lo posicionamos
    &::after{
        position: absolute;
        content: "";
        background: linear-gradient(to bottom, darken(var.$azul-violeta, 5%) 20%, lighten(var.$azul-real, 4.5%) 100%);
        width: 20rem;
        height: 20rem;
        left: calc(50% - 10rem);
        top: calc(50% - 15rem);
        clip-path: circle(50%);
    }

    &__titulo{
        color: var.$lavanda;
        margin-bottom: 0;
    }

    &__valor{
        color: var.$blanco;
        font-size: 8rem;
        font-weight: var.$black;
        margin-bottom: 0;
        z-index: 1;
    }

    &__maximo{
        margin-top: 0;
        text-align: center;
        z-index: 1;
    }

    &__calificacion{
        margin-bottom: 0;
    }

    &__texto{
        text-align: center;
        line-height: 1.5;
    }
}